/* Tier badges with glow effects */
.tier-premier{
  border-width: 1px;
  border-color: hsl(var(--tier-premier) / 0.4);
  background-color: hsl(var(--tier-premier) / 0.2);
  color: hsl(var(--tier-premier));
    box-shadow: var(--shadow-glow-pink);
}
.tier-enhanced{
  border-width: 1px;
  border-color: hsl(var(--tier-enhanced) / 0.4);
  background-color: hsl(var(--tier-enhanced) / 0.2);
  color: hsl(var(--tier-enhanced));
    box-shadow: var(--shadow-glow-cyan);
}
.tier-free{
  border-width: 1px;
  border-color: hsl(var(--tier-free) / 0.4);
  background-color: hsl(var(--tier-free) / 0.2);
  color: hsl(var(--tier-free));
}
/* Gradient backgrounds */
.gradient-brand {
    background: linear-gradient(135deg, hsl(330 80% 55%) 0%, hsl(270 80% 60%) 100%);
  }
.gradient-action {
    background: linear-gradient(90deg, hsl(330 80% 55%) 0%, hsl(350 80% 65%) 100%);
  }
/* Gradient text */
.gradient-text {
    background: linear-gradient(135deg, hsl(330 80% 55%) 0%, hsl(270 80% 60%) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
/* Gradient border effect */
.gradient-border {
    position: relative;
    background: hsl(var(--card));
    border-radius: var(--radius);
  }
.gradient-border::before {
    content: "";
    position: absolute;
    inset: -1px;
    padding: 1px;
    border-radius: calc(var(--radius) + 1px);
    background: linear-gradient(135deg, hsl(330 80% 55%) 0%, hsl(270 80% 60%) 100%);
    -webkit-mask:
      linear-gradient(#fff 0 0) content-box,
      linear-gradient(#fff 0 0);
    mask:
      linear-gradient(#fff 0 0) content-box,
      linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
    pointer-events: none;
  }
/* Glow effects */
/* Hover glow transition */
.hover-glow{
  transition-property: box-shadow;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
  animation-duration: 300ms;
}
.hover-glow:hover {
    box-shadow: var(--shadow-glow-pink);
  }
